
.Login-wrapper {
  height: 97vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-image: url("./appBack.jpg");
  background-size: 100% 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
 
}

.Login-formWrapper {
  margin-top: 8%;
  margin-left: 56%;
  width: 26%;
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
  border-radius: 10px;
  box-shadow: 0px 10px 50px #555;
  background-color: #155c62de;
  transition: 0.3s all;
}

#Login-Label1, #Login-Label2{
  color: #bdbca1;
  font-weight: 500;
}


#Login-title {
  text-align: center;
  width: 100%;
  color: rgb(201, 198, 173);
  font-weight: 500;
}

#Login-authButton button{
  background-color: #1e7091;
  color: #d6cca0;
  border: 2px solid #fff;
  width: 80%;
  padding: 8px 0px;
  margin: 0 10%;
  font-size: 1em;
  font-weight: lighter;
  letter-spacing: 1px;
  margin-bottom: 0.25em;
  cursor: pointer;
}
#Login-authButton button:hover, 
#Login-continueButton:hover{
  color: #fff;
  background-color: #a78b6b;
  border: 2px solid #eeeae8;
}

#Login-continueButton {
  position: inherit;
  text-align: center;
  font-weight: normal;
  color:#eeeae8 ;
  background-color: #1e7091;
  border: 2px solid #eeeae8;
  outline: none;
  width: 30%;
  padding: 5px 0px;
  margin-top: 30px;
  margin-bottom: 0.45em;
  cursor: pointer;
  float: left;
}


#Login-registerButton, 
#Login-registerButton a{
  color: rgb(185, 184, 184);
  font-weight: 500;
  font-size: 12px;
  text-align: center;
  outline: 0;
  margin-left: 4px;
}

#Login-registerButton a:hover{
  color: #d6cca0;
}
